.main {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.app_section_container {
    position: relative;
    /* width: calc(100% - 300px);
    top: 0;
    left: 150px; */
    padding: 0 80px;
    z-index: 2;
    max-width: 1200px;
}
.blur {
    filter: blur(5px) brightness(0.7);
    transition: var(--transition);
    pointer-events: none;
    user-select: none;
}

@media screen and (max-width:900px) {
    .app_section_container {
        /* padding-top: 80px !important; */
        padding: 0 20px;
    }
    
}